#coca-popup {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 99999999;
	text-align: center;
	transition: 300ms ease-in-out;

	.shortcode-container,
	.elementor-widget-container {
		display: none;
	}

	&.hide {
		display: none;
	}

	&.view .popup-container {
		animation: coca-zoom-in-zoom-out 300ms ease;

		&::before {

			/* position the div in center */
			position: absolute;
			top: 50%;
			left: 50%;
			display: block;

			width: 40px;
			height: 40px;
			margin: 0;
			content: "";
			border-color: #fff transparent #fff transparent;
			border-style: solid;
			border-width: 2px;
			border-radius: 50%;
			transform: translate(-50%, -50%);
			animation: coca-dual-ring-anime 1000ms linear infinite;
		}
	}

	&.loaded .popup-wrapper .popup-container {
		//align-items: normal;

		> :first-child {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 100vw;
			height: 100vh;
			padding: 60px;

			@media screen and (max-width: 500px) {
				padding: 40px;
			}
		}

		.shortcode-container,
		.elementor-widget-container {
			display: block;
			width: 100%;
			height: auto;
		}

		.coca-image-compare-wrapper {
			display: flex;
			//flex-direction: column;
			justify-content: center;
			//align-items: center;

			&,
			.coca-image-compare-container,
			img {
				width: 100%;
			}


			.coca-image-compare-container {
				max-width: inherit;
				animation: coca-zoom-in-zoom-out 300ms ease;
				//transition: 1000ms ease-in-out;
			}
		}
	}

	.popup-wrapper {
		position: relative;
		z-index: 1000000;
		box-sizing: border-box;
		width: 100%;
		height: 100%;
		overflow: auto;
		color: #000;
		background: rgba(0, 0, 0, 0.65);
		border-radius: 0;
		transition: 300ms ease-in-out;

		.popup-close {
			position: absolute;
			top: 0;
			right: 0;
			z-index: 2;
			display: block;
			padding: 25px;
			line-height: 0;
			color: #fff;
			cursor: pointer;
			transition: 0.5s;

			&:hover,
			&:focus,
			&:active {
				color: #f00;
			}

			* {
				pointer-events: none;
			}
		}

		.popup-container {

			position: fixed;
			top: 50%;
			left: 50%;
			display: flex;
			align-items: center;
			//flex-direction: column;
			justify-content: center;
			width: 100%;
			height: 100%;
			transform: translate(-50%, -50%);

			.popup-button,
			.coca-image-compare-wrapper {
				display: none;
			}
		}
	}
}

@keyframes coca-zoom-in-zoom-out {

	0% {
		transform: scale(.3, .3);
	}

	100% {
		transform: scale(1, 1);
	}
}

@keyframes coca-dual-ring-anime {

	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(360deg);
	}
}
